import { Component } from '@angular/core';

@Component({
    selector: 'app-root',
    templateUrl: './app.grocery.html',
    styleUrls: ['./app.grocery.css']
})
export class GroceryComponent {
    title = 'Shopping App';
    item: string;
    items = [];
    onClick(){
        this.items.push({name: this.item});
        this.item = '';
    }
}